body{
    background: black;
    color: white;
}

.coin-container .coin{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    padding: 15px;
    border-radius: 40px;
    border: solid white 2px;
}

.coin-row{
padding: 0 100px;
}

.coin img{
    width: 40px;
    background: white;
    border-radius: 50%;
    padding: 10px;
}

.coin h1{
    width: 250px;
    margin: 0 50px;
    font-size: 22px;
    color: #ffafbd ;

}

.coin .coin-volumes{
    width: 150px;
    color: #8A25D2;
}

.coin .coin-percent{
    width: 50px;
}

.coin .coin-symble{
    /* flex: 1; */
    width: 50px;
    color: rgb(226, 226, 145);
}

.coin-data{
    width: 200px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex: 2;
    margin-left: 30px;
}

.coin .coin-price{
    width: 100px;
    text-align: left;

    color: #fea674;
}