*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.coin-app .coin-search .coin-text{
    text-align: center;
    margin: 60px 0 0 0;
}

.coin-app .coin-search form{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.coin-app .coin-search .coin-input {
    width: 600px;
    height: 40px;
    padding: 0 10px;
    border-radius: 20px;
    outline: white solid 1px;
    border: none;
    margin: 40px auto;
}